.parties-container-list {
  flex-direction: row;
  gap: 10px;
  padding-top: 20px;
  display: flex;
}

.parties-container-item {
  width: 33%;
  box-sizing: border-box;
  padding: 10px;
}

.parties-container-item-title {
  text-align: center;
  padding-bottom: 10px;
  font-size: 14px;
  font-weight: bold;
}

.parties-container-details span {
  font-weight: bold;
}

.print-button {
  color: #020008;
  text-align: center;
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  display: block;
}

.print-button:hover {
  background-color: #ece1e1;
}

